.modal {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: scale(0);
    transition: 0.5s;
    opacity: 0;
    pointer-events: none;
}

.modal.active {
    opacity: 1;
    transform: scale(1);
    pointer-events: all;
}

.modal__content {
    width: 400px;
    height: 200px;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    z-index: 3;
    border-radius: 12px;
    background: cornsilk;
    transition: 0.4s all;
}

.modal__content.active {
    transform: scale(1);
}

.books-popup-clear {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 25px;
    padding-top: 30px;
    .button-wrap {
        width: 200px;
        display: flex;
        justify-content: space-between;
        margin-top: 30px;
        .btn {
            width: 70px;
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 3px solid #000;
            border-radius: 30px;
            background: #ffcc34;
            cursor: pointer;
        }
    }
}
